.header-container {
  width: 100%;
  background: black;
  padding: 2.5rem 15rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #313131;
}

.nav-links {
  display: flex;
  justify-content: space-around;
  text-decoration: none;
  align-items: center;
  color: white;
}
.nav-links a {
  text-decoration: none;
  color: white;
  font-size: 0.9rem;
  font-weight: 500;
  margin-right: 25px;
}
.cart-img1
{
  display: none;
  position: relative;
}
.cart-img2
{
  position: relative;
}
.cart-counter2
{
  color: white;
  position: absolute;
  background: var(--orange);
  border-radius: 50%;
  padding: 0.05rem 0.35rem;
  top: -1rem;
  left: 0.7rem;
  font-size: 0.9rem;
}
@media (max-width: 1150px) {
  .header-container {
    padding: 2.5rem 8rem;
  }
}
@media (max-width: 900px) {
  .header-container {
    padding: 2.5rem 3rem;
  }
}
@media (max-width: 600px) {
  .header-container {
    flex-direction: column;

    padding: 1rem;
  }
  .cart-img1 {
    display: inline;
  }
  .cart-img2 {
    display: none;
  }
  .logo-container
  {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  .nav-links
  {
    width: 100%;
    justify-content: space-between;
  }
  .nav-links a {
    margin-right:0;
    font-size: 0.8rem;
}
.cart-counter2
{
  top: -0.8rem;
  font-size: 0.8rem;
}
}
